import React, { useEffect, useRef, useState } from "react";
import { useNavigate } from "react-router-dom";
import { useParams } from "react-router-dom";
import { Button } from "primereact/button";
import { Card } from "primereact/card";
import { Dropdown } from "primereact/dropdown";
import { InputSwitch } from "primereact/inputswitch";
import { InputText } from "primereact/inputtext";
import { InputTextarea } from "primereact/inputtextarea";
import { Message } from "primereact/message";
import { shallowEqual } from "react-redux";
import { useAppDispatch, useAppSelector } from "../../utilities/hooks";
import { accountActions } from "../../store/account/accountSlice";
import { campaignActions } from "../../store/campaign/campaignSlice";
import { leadActions } from "../../store/lead/leadSlice";
import { userActions } from "../../store/user/userSlice";
import { IAppState } from "../../models/appState";
import { ILead } from "../../models/lead";
import { v4 as uuidv4 } from "uuid";
import { leadSourceData } from "../../utilities/globals";

const LeadEdit: React.FC = () => {
  const statusData = ["New", "Assigned", "Closed", "Pending", "Rejected"];

  const defaultItem: ILead = {
    id: "",
    firstName: "",
    lastName: "",
    title: "",
    description: "",
    assignedUserId: "",
    assignedUserLastName: "",
    department: "",
    doNotCall: false,
    phoneHome: "",
    phoneMobile: "",
    phoneWork: "",
    phoneOther: "",
    phoneFax: "",
    primaryAddressStreet: "",
    primaryAddressCity: "",
    primaryAddressState: "",
    primaryAddressPostalcode: "",
    primaryAddressCountry: "",
    altAddressStreet: "",
    altAddressCity: "",
    altAddressState: "",
    altAddressPostalcode: "",
    altAddressCountry: "",
    referedBy: "",
    leadSource: "",
    leadSourceDescription: "",
    status: "",
    statusDescription: "",
    campaignId: "",
    email1: "",
    email2: "",
    emailOptout: false,
    accountId: "",
    accountName: "",
  };
  const navigateTo = "/leads";
  const navigate = useNavigate();
  const [subtitle, setSubtitle] = useState("Create");
  const [item, setItem] = useState(defaultItem);
  const [dirty, setDirty] = useState(false); // if form input has been changed, dirty is true
  const [valid, setValid] = useState(false); // if form input is valid, valid is true
  const [errors, setErrors] = useState({}); // indicates if fields are valid
  const params = useParams();
  const memo = useRef(true);

  const [lead, isFetching, isError, accounts, campaigns, users] =
    useAppSelector(
      (state: IAppState) => [
        state.leads.lead,
        state.leads.isFetching,
        state.leads.isError,
        state.accounts.accounts,
        state.campaigns.campaigns,
        state.users.users,
      ],
      shallowEqual
    );
  const dispatch = useAppDispatch();

  useEffect(() => {
    if (memo.current) {
      memo.current = false;
      dispatch(accountActions.getAccounts());
      dispatch(campaignActions.getCampaigns());
      dispatch(userActions.getUsers());
      if (params.mode === "edit") {
        setSubtitle("Edit");
        const id = params.id;
        if (id) {
          dispatch(leadActions.getLeadById(id));
        }
      }
    }
  }, [params, dispatch]);

  useEffect(() => {
    if (
      params.mode === "edit" &&
      item.id !== lead?.id &&
      !isFetching &&
      !isError
    ) {
      setItem(lead);
    }
  }, [params, item, lead, isFetching, isError]);

  useEffect(() => {
    if (!dirty) {
      return;
    }

    setValid((item.lastName || "").trim().length > 0);
  }, [dirty, item]);

  const onChange = (e: any) => {
    setDirty(true);
    const {
      target: { name: tname, value: tvalue },
    } = e;

    if (tname === "assignedUserId") {
      const user = users.find((value) => value.id === tvalue);
      if (user) {
        setItem({
          ...item,
          [tname]: tvalue,
          assignedUserLastName: user.lastName,
        });
      }
    } else if (tname === "accountId") {
      const account = accounts.find((value) => value.id === tvalue);
      if (account) {
        setItem({ ...item, [tname]: tvalue, accountName: account.name });
      }
    } else {
      setItem({ ...item, [tname]: tvalue });
    }

    if (tname === "lastName") {
      setErrors({
        ...errors,
        [tname]: tvalue ? tvalue.toString().trim().length === 0 : true,
      });
    }
  };

  const onCopyPrimaryClick = () => {
    setDirty(true);
    setItem({
      ...item,
      altAddressStreet: item.primaryAddressStreet,
      altAddressCity: item.primaryAddressCity,
      altAddressState: item.primaryAddressState,
      altAddressPostalcode: item.primaryAddressPostalcode,
      altAddressCountry: item.primaryAddressCountry,
    });
  };

  const onCopyAltClick = () => {
    setDirty(true);
    setItem({
      ...item,
      primaryAddressStreet: item.altAddressStreet,
      primaryAddressCity: item.altAddressCity,
      primaryAddressState: item.altAddressState,
      primaryAddressPostalcode: item.altAddressPostalcode,
      primaryAddressCountry: item.altAddressCountry,
    });
  };

  const onSaveClick = async () => {
    if (subtitle !== "Edit") {
      item.id = uuidv4();
    }

    dispatch(leadActions.updateLead(item));
    setTimeout(() => {
      dispatch(leadActions.getLeads());
    }, 300);
    navigate(navigateTo);
  };

  const onCancelClick = async () => {
    navigate(navigateTo);
  };

  return (
    <>
      <h3 className="caption">Lead: {subtitle}</h3>
      <div className="grid">
        <div className="col-12">
          <span className="field-required">*</span> - Required field
        </div>
      </div>
      <Card>
        <div className="grid">
          <div className="col-12">
            <h3>Lead</h3>
          </div>
        </div>
        <div className="grid">
          <div className="col-12">
            <div className="grid">
              <div className="col">
                <label htmlFor="LeadSource">Lead Source</label>
              </div>
              <div className="col">
                <Dropdown
                  id="LeadSource"
                  name="leadSource"
                  options={leadSourceData}
                  value={item.leadSource}
                  onChange={onChange}
                  className="p-combobox"
                />
              </div>
              <div className="col-fixed p-fixed hidden-xs">&nbsp;</div>
              <div className="col">
                <label htmlFor="Status">Status</label>
              </div>
              <div className="col">
                <Dropdown
                  id="Status"
                  name="status"
                  options={statusData}
                  value={item.status}
                  onChange={onChange}
                  className="p-combobox"
                />
              </div>
            </div>
          </div>
          <div className="col-12">
            <div className="grid">
              <div className="col">
                <label htmlFor="LeadSourceDescription">
                  Lead Source Description
                </label>
              </div>
              <div className="col">
                <InputText
                  id="LeadSourceDescription"
                  name="leadSourceDescription"
                  maxLength={100}
                  value={item.leadSourceDescription}
                  onChange={onChange}
                />
              </div>
              <div className="col-fixed p-fixed hidden-xs">&nbsp;</div>
              <div className="col">
                <label htmlFor="StatusDescription">Status Description</label>
              </div>
              <div className="col">
                <InputText
                  id="StatusDescription"
                  name="statusDescription"
                  maxLength={100}
                  value={item.statusDescription}
                  onChange={onChange}
                />
              </div>
            </div>
          </div>
          <div className="col-12">
            <div className="grid">
              <div className="col">
                <label htmlFor="Campaign">Campaign</label>
              </div>
              <div className="col">
                <Dropdown
                  id="Campaign"
                  name="campaignId"
                  options={campaigns}
                  optionLabel="name"
                  optionValue="id"
                  value={item.campaignId}
                  onChange={onChange}
                  className="p-combobox"
                />
              </div>
              <div className="col-fixed p-fixed hidden-xs">&nbsp;</div>
              <div className="col">
                <label htmlFor="ReferedBy">Refered By</label>
              </div>
              <div className="col">
                <InputText
                  id="ReferedBy"
                  name="referedBy"
                  maxLength={100}
                  value={item.referedBy}
                  onChange={onChange}
                />
              </div>
            </div>
          </div>
          <div className="col-12">
            <div className="grid">
              <div className="col">
                <label htmlFor="FirstName">First Name</label>
              </div>
              <div className="col">
                <InputText
                  id="FirstName"
                  name="firstName"
                  maxLength={100}
                  placeholder="Joe"
                  value={item.firstName}
                  onChange={onChange}
                />
              </div>
              <div className="col-fixed p-fixed hidden-xs">&nbsp;</div>
              <div className="col">
                <label htmlFor="LastName">
                  Last Name <span className="field-required">*</span>
                </label>
              </div>
              <div className="col">
                <InputText
                  id="LastName"
                  name="lastName"
                  maxLength={100}
                  placeholder="Doe"
                  value={item.lastName}
                  onChange={onChange}
                />
                {(errors as any)?.lastName && (
                  <Message severity="error" text="Last Name is required." />
                )}
              </div>
            </div>
          </div>
          <div className="col-12">
            <div className="grid">
              <div className="col">
                <label htmlFor="Title">Title</label>
              </div>
              <div className="col">
                <InputText
                  id="Title"
                  name="title"
                  maxLength={100}
                  value={item.title}
                  onChange={onChange}
                />
              </div>
              <div className="col-fixed p-fixed hidden-xs">&nbsp;</div>
              <div className="col">
                <label htmlFor="AccountName">Account Name</label>
              </div>
              <div className="col">
                <Dropdown
                  id="AccountName"
                  name="accountId"
                  options={accounts}
                  optionLabel="name"
                  optionValue="id"
                  value={item.accountId}
                  onChange={onChange}
                  className="p-combobox"
                />
              </div>
            </div>
          </div>
          <div className="col-12">
            <div className="grid">
              <div className="col">
                <label htmlFor="Email1">Primary Email</label>
              </div>
              <div className="col">
                <InputText
                  id="Email1"
                  name="email1"
                  maxLength={255}
                  placeholder="Example jdoe@example.com"
                  value={item.email1}
                  onChange={onChange}
                />
              </div>
              <div className="col-fixed p-fixed hidden-xs">&nbsp;</div>
              <div className="col">
                <label htmlFor="Email2">Other Email</label>
              </div>
              <div className="col">
                <InputText
                  id="Email2"
                  name="email2"
                  maxLength={255}
                  placeholder="Example jdoe@example.com"
                  value={item.email2}
                  onChange={onChange}
                />
              </div>
            </div>
          </div>
          <div className="col-12">
            <div className="grid">
              <div className="col">
                <label htmlFor="EmailOptOut">Email Opt Out</label>
              </div>
              <div className="col">
                <div className="p-fixed-switch">
                  <InputSwitch
                    id="EmailOptOut"
                    name="emailOptout"
                    checked={item.emailOptout || false}
                    value="0"
                    onChange={onChange}
                  />
                </div>
              </div>
              <div className="col-fixed p-fixed hidden-xs">&nbsp;</div>
              <div className="col">
                <label htmlFor="Department">Department</label>
              </div>
              <div className="col">
                <InputText
                  id="Department"
                  name="department"
                  maxLength={255}
                  value={item.department}
                  onChange={onChange}
                />
              </div>
            </div>
          </div>
          <div className="col-12">
            <div className="grid">
              <div className="col">
                <label htmlFor="AssignedTo">Assigned To</label>
              </div>
              <div className="col">
                <Dropdown
                  id="AssignedTo"
                  name="assignedUserId"
                  options={users}
                  optionLabel="lastName"
                  optionValue="id"
                  value={item.assignedUserId}
                  onChange={onChange}
                  className="p-combobox"
                />
              </div>
              <div className="col-fixed p-fixed hidden-xs">&nbsp;</div>
              <div className="col hidden-xs">&nbsp;</div>
              <div className="col hidden-xs">
                <div className="p-fixed-switch">&nbsp;</div>
              </div>
            </div>
          </div>
        </div>
      </Card>
      <div className="grid">
        <div className="col-12">&nbsp;</div>
      </div>
      <Card>
        <div className="grid">
          <div className="col-12">
            <h3>Phones</h3>
          </div>
        </div>
        <div className="grid">
          <div className="col-12">
            <div className="grid">
              <div className="col">
                <label htmlFor="DoNotCall">Do Not Call</label>
              </div>
              <div className="col">
                <InputSwitch
                  id="DoNotCall"
                  name="doNotCall"
                  checked={item.doNotCall || false}
                  value="0"
                  onChange={onChange}
                />
              </div>
              <div className="col-fixed p-fixed hidden-xs">&nbsp;</div>
              <div className="col">
                <label htmlFor="PhoneOffice">Office Phone</label>
              </div>
              <div className="col">
                <InputText
                  id="PhoneOffice"
                  name="phoneWork"
                  maxLength={25}
                  value={item.phoneWork}
                  onChange={onChange}
                />
                {(errors as any)?.lastName && (
                  <Message severity="error" text="Last Name is required." />
                )}
              </div>
            </div>
          </div>
          <div className="col-12">
            <div className="grid">
              <div className="col">
                <label htmlFor="PhoneMobile">Mobile Phone</label>
              </div>
              <div className="col">
                <InputText
                  id="PhoneMobile"
                  name="phoneMobile"
                  maxLength={25}
                  value={item.phoneMobile}
                  onChange={onChange}
                />
              </div>
              <div className="col-fixed p-fixed hidden-xs">&nbsp;</div>
              <div className="col">
                <label htmlFor="PhoneHome">Home Phone</label>
              </div>
              <div className="col">
                <InputText
                  id="PhoneHome"
                  name="phoneHome"
                  maxLength={25}
                  value={item.phoneHome}
                  onChange={onChange}
                />
              </div>
            </div>
          </div>
          <div className="col-12">
            <div className="grid">
              <div className="col">
                <label htmlFor="PhoneOther">Other Phone</label>
              </div>
              <div className="col">
                <InputText
                  id="PhoneOther"
                  name="phoneOther"
                  maxLength={25}
                  value={item.phoneOther}
                  onChange={onChange}
                />
              </div>
              <div className="col-fixed p-fixed hidden-xs">&nbsp;</div>
              <div className="col">
                <label htmlFor="PhoneFax">Fax</label>
              </div>
              <div className="col">
                <InputText
                  id="PhoneFax"
                  name="phoneFax"
                  maxLength={25}
                  value={item.phoneFax}
                  onChange={onChange}
                />
              </div>
            </div>
          </div>
        </div>
      </Card>
      <div className="grid">
        <div className="col-12">&nbsp;</div>
      </div>
      <Card>
        <div className="grid">
          <div className="col-12">
            <h3>Address</h3>
          </div>
        </div>
        <div className="grid">
          <div className="col">
            <h4>Primary Address</h4>
          </div>
          <div className="col-fixed p-fixed-10 hidden-xs">&nbsp;</div>
          <div className="col hidden-xs">
            <h4>Other Address</h4>
          </div>
        </div>
        <div className="grid">
          <div className="col-12">
            <div className="grid">
              <div className="col">
                <label htmlFor="PrimaryStreet">Street</label>
              </div>
              <div className="col">
                <InputText
                  id="PrimaryStreet"
                  name="primaryAddressStreet"
                  maxLength={150}
                  value={item.primaryAddressStreet}
                  onChange={onChange}
                />
              </div>
              <div className="col-fixed p-fixed-10 hidden-xs">&nbsp;</div>
              <div className="col hidden-xs">
                <label htmlFor="AltStreet1">Street</label>
              </div>
              <div className="col hidden-xs">
                <InputText
                  id="AltStreet1"
                  name="altAddressStreet"
                  maxLength={150}
                  value={item.altAddressStreet}
                  onChange={onChange}
                />
              </div>
            </div>
          </div>
          <div className="col-12">
            <div className="grid">
              <div className="col">
                <label htmlFor="PrimaryCity">City</label>
              </div>
              <div className="col">
                <InputText
                  id="PrimaryCity"
                  name="primaryAddressCity"
                  maxLength={100}
                  value={item.primaryAddressCity}
                  onChange={onChange}
                />
              </div>
              <div className="col-fixed p-fixed-10 hidden-xs">
                <Button
                  icon="pi pi-arrow-circle-right"
                  className="p-button-rounded p-button-text p-button-lg"
                  tooltip="Copy Primary Address"
                  onClick={onCopyPrimaryClick}
                />
              </div>
              <div className="col hidden-xs">
                <label htmlFor="AltCity1">City</label>
              </div>
              <div className="col hidden-xs">
                <InputText
                  id="AltCity1"
                  name="altAddressCity"
                  maxLength={100}
                  value={item.altAddressCity}
                  onChange={onChange}
                />
              </div>
            </div>
          </div>
          <div className="col">
            <div className="grid">
              <div className="col">
                <label htmlFor="PrimaryState">State</label>
              </div>
              <div className="col">
                <InputText
                  id="PrimaryState"
                  name="primaryAddressState"
                  maxLength={100}
                  value={item.primaryAddressState}
                  onChange={onChange}
                />
              </div>
              <div className="col-fixed p-fixed-10 hidden-xs">&nbsp;</div>
              <div className="col hidden-xs">
                <label htmlFor="AltState1">State</label>
              </div>
              <div className="col hidden-xs">
                <InputText
                  id="AltState1"
                  name="altAddressState"
                  maxLength={100}
                  value={item.altAddressState}
                  onChange={onChange}
                />
              </div>
            </div>
          </div>
          <div className="col-12">
            <div className="grid">
              <div className="col">
                <label htmlFor="PrimaryPostalCode">Postal Code</label>
              </div>
              <div className="col">
                <InputText
                  id="PrimaryPostalCode"
                  name="primaryAddressPostalcode"
                  maxLength={20}
                  value={item.primaryAddressPostalcode}
                  onChange={onChange}
                />
              </div>
              <div className="col-fixed p-fixed-10 hidden-xs">
                <Button
                  icon="pi pi-arrow-circle-left"
                  className="p-button-rounded p-button-text p-button-lg"
                  tooltip="Copy Alt Address"
                  onClick={onCopyAltClick}
                />
              </div>
              <div className="col hidden-xs">
                <label htmlFor="AltPostalCode1">Postal Code</label>
              </div>
              <div className="col hidden-xs">
                <InputText
                  id="AltPostalCode1"
                  name="altAddressPostalcode"
                  type="text"
                  maxLength={20}
                  value={item.altAddressPostalcode}
                  onChange={onChange}
                />
              </div>
            </div>
          </div>
          <div className="col-12">
            <div className="grid">
              <div className="col">
                <label htmlFor="PrimaryCountry">Country</label>
              </div>
              <div className="col">
                <InputText
                  id="PrimaryCountry"
                  name="primaryAddressCountry"
                  maxLength={255}
                  value={item.primaryAddressCountry}
                  onChange={onChange}
                />
              </div>
              <div className="col-fixed p-fixed-10 hidden-xs">&nbsp;</div>
              <div className="col hidden-xs">
                <label htmlFor="AltCountry1">Country</label>
              </div>
              <div className="col hidden-xs">
                <InputText
                  id="AltCountry1"
                  name="altAddressCountry"
                  maxLength={255}
                  value={item.altAddressCountry}
                  onChange={onChange}
                />
              </div>
            </div>
          </div>
          <div className="col-12 hidden-sm hidden-md hidden-lg">
            <div className="grid">
              <div className="col">
                <Button
                  icon="pi pi-arrow-circle-down"
                  className="p-button-rounded p-button-text p-button-lg"
                  tooltip="Copy Primary Address"
                  onClick={onCopyPrimaryClick}
                />
              </div>
              <div className="col">
                <Button
                  icon="pi pi-arrow-circle-up"
                  className="p-button-rounded p-button-text p-button-lg"
                  tooltip="Copy Alt Address"
                  onClick={onCopyAltClick}
                />
              </div>
            </div>
          </div>
          <div className="col-12 hidden-sm hidden-md hidden-lg">
            <h4>Alt Address</h4>
          </div>
          <div className="col-12 hidden-sm hidden-md hidden-lg">
            <div className="grid">
              <div className="col-12">
                <label htmlFor="AltStreet2">Street</label>
              </div>
              <div className="col-12">
                <InputText
                  id="AltStreet2"
                  name="altAddressStreet"
                  maxLength={150}
                  value={item.altAddressStreet}
                  onChange={onChange}
                />
              </div>
            </div>
          </div>
          <div className="col-12 hidden-sm hidden-md hidden-lg">
            <div className="grid">
              <div className="col-12">
                <label htmlFor="AltCity2">City</label>
              </div>
              <div className="col-12">
                <InputText
                  id="AltCity2"
                  name="altAddressCity"
                  maxLength={100}
                  value={item.altAddressCity}
                  onChange={onChange}
                />
              </div>
            </div>
          </div>
          <div className="col-12 hidden-sm hidden-md hidden-lg">
            <div className="grid">
              <div className="col-12">
                <label htmlFor="AltState2">State</label>
              </div>
              <div className="col-12">
                <InputText
                  id="AltState2"
                  name="altAddressState"
                  maxLength={100}
                  value={item.altAddressState}
                  onChange={onChange}
                />
              </div>
            </div>
          </div>
          <div className="col-12 hidden-sm hidden-md hidden-lg">
            <div className="grid">
              <div className="col-12">
                <label htmlFor="AltPostalCode2">Postal Code</label>
              </div>
              <div className="col-12">
                <InputText
                  id="AltPostalCode2"
                  name="altAddressPostalcode"
                  maxLength={20}
                  value={item.altAddressPostalcode}
                  onChange={onChange}
                />
              </div>
            </div>
          </div>
          <div className="col-12 hidden-sm hidden-md hidden-lg">
            <div className="grid">
              <div className="col-12">
                <label htmlFor="AltCountry2">Country</label>
              </div>
              <div className="col-12">
                <InputText
                  id="AltCountry2"
                  name="altAddressCountry"
                  maxLength={255}
                  value={item.altAddressCountry}
                  onChange={onChange}
                />
              </div>
            </div>
          </div>
        </div>
      </Card>
      <div className="grid">
        <div className="col-12">&nbsp;</div>
      </div>
      <Card>
        <div className="grid">
          <div className="col-12">
            <h3>Description</h3>
          </div>
        </div>
        <div className="grid">
          <div className="col-12">
            <InputTextarea
              name="description"
              value={item.description}
              onChange={onChange}
              autoResize
            />
          </div>
        </div>
      </Card>
      <div className="grid">
        <div className="col-12">&nbsp;</div>
        <div className="col-12">
          <Button
            icon="pi pi-check"
            className="p-button-rounded p-button-outlined"
            tooltip="Save"
            disabled={!valid}
            onClick={onSaveClick}
          />
          &nbsp; &nbsp;
          <Button
            icon="pi pi-times"
            className="p-button-rounded p-button-outlined"
            tooltip="Back to List"
            onClick={onCancelClick}
          />
        </div>
      </div>
    </>
  );
};

export default LeadEdit;
